<template>
	<div id="addDispatching">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">添加配送</div>
        <div class="van-nav-bar__right" @click="addRental">
          发布
        </div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff login_bottom">
        <img src="static/img/icon_xm_tjps11.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="text" placeholder="姓名" v-model="uname"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <img src="static/img/icon_shouji--mf11.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="text" placeholder="联系方式" v-model="tell"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <img src="static/img/icon_dw_dw11.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="text" placeholder="起始地址" v-model="startpath"/>
      </div>
      <div class="login_bar background_color_ffffff">
        <img src="static/img/icon_dw_dw11.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="text" placeholder="送达地址" v-model="endpath"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <span class="img_top_center width_5">标题</span>
        <input class="font_14 login_input" type="text" placeholder="请输入标题" v-model="title"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <textarea class="font_14 login_input1" style="width: 100%" type="text" placeholder="请输入详细描述" v-model="content"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img :src="item" class="img_service" v-for="(item,index) in imgList" @click="choice(index)"/>
        <img src="static/img/icon_tjtp_tjps.png" class="img_service" @click="uploadImg"/>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'addDispatching',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        title:'',
        content:'',
        startpath:'',
        endpath:'',
        uname:'',
        tell:'',
        type:4,
        sameimg:'',
        imgList:[],
			}
		},
		methods:{
      choice(index){
        this.$dialog.confirm({
          message: '请选择切换或删除该张图片',
          confirmButtonText:'删除',
          cancelButtonText:'切换'
        }).then(() => {
          let newImgList = [];
          for(let i=0;i<this.imgList.length;i++){
            if(i!=index){
              newImgList.push(this.imgList[i]);
            }
          }
          this.imgList = newImgList;
        }).catch(() => {
          this.changeImg(index)
        });
      },
      changeImg(dataMap){
        let $this = this;
        dsBridge.call('uploadImg');
        dsBridge.register('addImgCallback', function(rpdata) {
          let imgList1 = [];
          for(let i =0;i<$this.imgList.length;i++){
            if(i==dataMap){
              imgList1.push(rpdata)
            }else{
              imgList1.push($this.imgList[i])
            }
          }
          $this.imgList = imgList1;
        })
      },
      uploadImg(){
        let $this = this;
        if(this.imgList.length==6){
          $this.$toast({
            message: '最多可以上传6张图片!',
            position: "bottom"
          });
        }else if(this.imgList.length<6){
          dsBridge.call('uploadImg');
          dsBridge.register('addImgCallback', function(rpdata) {
            // $this.imgList.push(rpdata);
            if((rpdata.split(',').length+$this.imgList.length)>6){
              $this.$toast({
                message: '最多可以上传6张图片!',
                position: "bottom"
              });
            }else{
              for(let i=0;i<rpdata.split(',').length;i++){
                $this.imgList.push(rpdata.split(',')[i]);
              }
            }
          })
        }
      },
      addRental(){
        this.sameimg = this.imgList.join(',');
        let $this = this;
        let obj = {
          userid: $this.userId,
          title: $this.title,
          content: $this.content,
          startpath: $this.startpath,
          endpath: $this.endpath,
          uname: $this.uname,
          tell: $this.tell,
          sameimg: $this.sameimg,
          type: $this.type,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/addsamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '发布成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .img_service{
    width: 32.3%;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_span{
    position: relative;
    left: 46%;
    bottom: 32px;
    color: #ffffff;
  }
  .login_btn{
    width: 96%;
    margin-left: 2%;
  }
  .login_input1{
    margin-left: 0%;
    border: 0;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



